          },
          {
            type: "string",
            min: 4,
            message: "The password length cannot be less than 6 bits",
            trigger: "blur"
          }
        ]
      },
      formCustom: {
        passwd: "",
        passwdCheck: "",
        user: "",
        email: ""
      },
      ruleCustom: {
        passwd: [
          {
            required: true,
            message: "The password cannot be empty",
            trigger: "blur"
          },
          { validator: validatePass, trigger: "blur" }
        ],
        passwdCheck: [
          {
            required: true,
            message: "The password cannot be empty",
            trigger: "blur"
          },
          { validator: validatePassCheck, trigger: "blur" }
        ],
        name: [
          {
            required: true,
            message: "The name cannot be empty",
            trigger: "blur"
          }
        ],
        mail: [
          { type: "email", message: "Incorrect email format", trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    childEventHandler: function(res) {
      // res会传回一个data,包含属性mdValue和htmlValue，具体含义请自行翻译
      //this.arr[index].mdValue=res.mdValue

      if (chaTo) {
        buf = res.mdValue;
        this.arr[index].mdValue = buf;
        this.user.mdValue = buf;
        this.user.htmlValue = res.htmlValue;
        let postdata = this.$qs.stringify({
          name: this.user.name,
          contains: this.arr
        });
        counter = (counter + 1) % 70;
        if (counter == 1 && buf != "")
          this.$axios
            .post("https://www.cherryhuang.cn:8080/updata", postdata)
            .then(response => {
              if (response.data[0]) {
                this.$Message.success("自动保存成功");
              }
            })
            .catch(error => {
              console.log(error);
            });
      }
    },
    saveToHtml: function() {
      if (chaTo) {
        this.user.htmlValue =
          pre0.replace("{name}", this.user.name) +
          pre +
          this.user.htmlValue +
          stop;
        writer(title + " MarkdownNote.html", this.user.htmlValue, "utf-8");
        if (response.data[0]) {
          this.$Message.success("自动保存成功");
        }
      }
    },

    exitT: function() {
      this.logged = false;
      this.user.mdValue = "";
    },
    creatA: function() {
      this.modal10 = true;
      let b = {
        name: this.inputval,
        mdValue: "## Vue-markdownEditor\n"
      };
      this.arr.push(b);
    },
    delet: function(i) {
      this.arr.splice(i, 1);
    },
    show: function() {
      this.ifShowInfo = !this.ifShowInfo;
    },
    chaTo: function(mdValue, inx) {
      this.user.mdValue = mdValue;
      index = inx;
      chaTo = true;
      title = this.arr[inx].name;
    },
    save: function() {
      this.arr[index].mdValue = buf;
      this.user.mdValue = buf;
      let postdata = this.$qs.stringify({
        name: this.user.name,
        contains: this.arr
      });
      console.log(postdata);
      this.$axios
        .post("https://www.cherryhuang.cn:8080/updata", postdata)
        .then(response => {
          //console.log(response);
          //console.log(response.data);
          this.postData = response.data;
          if (response.data[0]) {
            this.$Message.success("保存成功");
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    toR: function() {
      this.toReg = !this.toReg;
    },
    toL: function() {
      this.toReg = false;
      this.ifLogin = !this.ifLogin;
    },
    backTo: function() {
      this.toReg = !this.toReg;
    },
    LoginSubmit: function() {
      let url = "https://www.cherryhuang.cn:8080/login?name=";
      let url1 = "&&password=";
      let name_user = this.formInline.user;
      let password = this.formInline.password;
      this.$axios
        .get(url + name_user + url1 + password)
        .then(response => {
          if (response.data[0]) {
            this.$Message.info("你好啊！ " + name_user + " 登陆成功");

            this.ifLogin = !this.ifLogin;
            this.logged = !this.logged;
            this.user.name = name_user;
            try {
              response.data.shift();
              this.arr = response.data[0];
            } catch (e) {
              console.log(e);
            }
          } else {
            this.$Message.info(" " + name_user + " 尚未创建或用户名不正确");
          }
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    handleRegiseterSubmit: function() {
      let url = "https://www.cherryhuang.cn:8080/register?name=";
      let url1 = "&&password=";
      let url2 = "&&email=";
      let name_user = this.formCustom.user;
      let password = this.formCustom.passwd;
      let email = this.formCustom.email;
      console.log(url + name_user + url1 + password + email);
      this.$axios
        .get(url + name_user + url1 + password + url2 + email)
        .then(response => {
          if (response.data[1]) {
            this.$Message.success("Success!");

            this.toReg = false;
          } else {
            this.$Message.error("Fail! User name has been  occupied!");
          }
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    handleReset(name) {
      this.toReg = false;
    }
  }
};
</script>
<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: left;
  color: #2c3e50;
  height: 100%;
}
.allContainer {
  height: 100%;
  background: #aaa;
}
.allContent {
  height: 100%;
  padding: 10%;
  background: #aaa;
}
.sideDonghua-enter-active,
.sideDonghua-leave-active {
  transition: 500ms all ease;
}
.sideDonghua-enter-active {
  transform: translate3d(0, 0, 0);
}
.sideDonghua-leave-active {
  transform: translate3d(100%, 0, 0);
}
.sideDonghua-enter {
  transform: translate3d(100%, 0, 0);
}
.sideDonghua-leave {
  transform: translate3d(0, 0, 0);
}

/* s */
.loginDonghua-enter-active,
.loginDonghua-leave-active {
  transition: 500ms all ease;
}
.loginDonghua-enter-active {
  transform: translate3d(0, 0, 0);
}
.loginDonghua-leave-active {
  transform: translate3d(0, 100%, 0);
}
.loginDonghua-enter {
  transform: translate3d(0, 100%, 0);
}
.loginDonghua-leave {
  transform: translate3d(0, 0, 0);
}
.left {
  float: left;
  width: 100%;
  height: 100%;
  background-color: #aaaaaa;
  margin-right: -100%;
  margin-bottom: -100%;
  padding: 5%;
}
.right {
  opacity: 0.8;
  float: right;
  width: 15%;
  height: 100%;
  background-color: #ccc;
  overflow: auto;
}
.middle {
  opacity: 0.8;
  background: aqua;
  color: #ffffff;
  overflow: auto;
  z-index: 9999;
  position: fixed;
  padding: 5px;
  text-align: center;
  width: 40%;
  height: 30%;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  right: 30.9%;
  top: 45%;
}
.font {
  padding-top: 8px;
}
.border {
  border-bottom: solid;
  border-bottom-width: 1px;
  border-bottom-color: aqua;
}
</style>



